.gallery {
  position: relative;
  margin-top: 80px;
  font-family: "Poppins", "Arial", sans-serif;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  p {
    margin: 0;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 120px;
    height: 1px;
    left: 0;
    width: 100vw;
    background-color: $line;
  }

  &--no-js {
    margin-top: 24px;
    margin-bottom: 100px;
    width: 400px;

    .gallery__item {
      border: none;
      border-bottom: 1px solid $menu-background;
    }

    &::after {
      display: none;
    }
  }

  &--js {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    margin: auto;
    display: none;
  }
}

.gallery__item {
  position: relative;

  h4 {
    margin: 0;
    font-weight: 400;
  }

  @include close-open(30px, 30px);
}

.gallery__slider {
  position: relative;
  margin: 0 25px 50px 25px;
  border-top: 1px solid $main-ground;
}

.gallery__price {
  position: absolute;
  top: 17px;

  &::after {
    content: "";
    position: absolute;
    top: -26px;
    left: 10px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: $menu-background;
  }

  &--low {
    left: 38px;

    &::before {
      content: "";
      position: absolute;
      top: -18px;
      left: 10px;
      width: 100px;
      height: 1px;
      background-color: $menu-background;
    }
  }

  &--high {
    right: 25px;
  }
}

.gallery__wrapper {
  display: flex;
  justify-content: space-between;

  @media (max-width: $tablet-width-only) {
    display: block;
  }
}

.gallery__filter {
  margin-right: 50px;
  width: 250px;

  li {
    margin-top: -1px;
    padding-top: 28px;
    padding-bottom: 27px;
    border: 1px solid $under-line;

    label {
      display: block;
      margin-left: 60px;
      margin-top: 13px;
      margin-bottom: 2px;
    }
  }

  h4 {
    margin-left: 25px;
  }

  &--close {
    display: none;
  }

  @media (max-width: $tablet-width-only) {
    display: none;

    &--no-js {
      display: block;
    }
  }
}

.gallery__filter-item {
  margin-top: 30px;

  label {
    position: relative;
    display: inline-flex;
    user-select: none;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -33px;
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 1px solid $menu-background;

      // @media (max-width: $mobile-width-only) {
      //   top: 0;
      // }
    }
  }

  input:checked + label::before {
    background-color: $menu-background;
  }

  label:hover,
  label:hover::before {
    opacity: 0.7;
  }
}

.gallery__filter-submit,
.gallery__filter-reset {
  width: 250px;
  height: 68px;
  font-size: 16px;
  line-height: 130%;
}

.gallery__filter-submit {
  margin-top: 35px;
  margin-bottom: 25px;
  border: none;
  color: $white;
  background-color: $menu-background;
}

.gallery__filter-reset {
  color: $letter;
  background-color: $white;
  border: 1px solid $menu-background;
}

.gallery__show-filter {
  display: none;

  @media (max-width: $tablet-width-only) {
    position: relative;
    display: block;
    margin-bottom: 50px;
    height: 68px;
    color: $white;
    background-color: $menu-background;

    &::after {
      content: "Filter";
      position: absolute;
      height: 12px;
      width: 39px;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }
}

.gallery__exhibits {
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    p,
    h4 {
      text-align: center;
    }

    p {
      margin-top: 25px;
      margin-bottom: 15px;
    }

    h4 {
      margin-top: 0;
      margin-bottom: 40px;
    }
  }

  img {
    @media (max-width: $tablet-width-only) {
      width: 206px;
      height: 208px;
    }
  }
}

.gallery__control {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  margin-bottom: 80px;

  ul {
    color: $grey-letter;
  }

  li {
    margin-right: 15px;
  }

  &--active,
  p {
    color: $letter;
  }
}

.gallery__close {
  @include close-button(23px, $white);

  border-radius: 50%;
  background-color: $black;
}

.gallery--show {
  display: block;
  width: 330px;
  height: 550px;
  box-shadow: 0 4px 30px $black;
  background-color: $white;

  form {
    margin-top: 24px;
  }

  li {
    border: none;
    border-bottom: 1px solid $menu-background;
  }
}
