.faq {
  margin-bottom: 108px;
  max-width: 970px;

  h2 {
    text-align: center;
    color: $letter;
    margin: 0 0 60px 0;
    font-weight: bold;
    font-size: 34px;
    line-height: 130%;
    letter-spacing: 0.03em;

    @media (max-width: $mobile-width-only) {
      margin-bottom: 40px;
    }
  }

  ul {
    list-style: none;
    padding: 0;
  }

  @media (max-width: $tablet-width-only) {
    margin-bottom: 84px;
    max-width: 678px;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 70px;
  }
}

.faq__question {
  position: relative;
  padding-top: 50px;
  padding-bottom: 49px;
  border: 1px solid $under-line;
  margin-top: -1px;

  h4,
  p {
    margin: 0;
    font-family: "Poppins", "Arial", sans-serif;
  }

  h4 {
    margin-left: 38px;
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;

    @media (max-width: $tablet-width-only) {
      margin-left: 50px;
    }

    @media (max-width: $mobile-width-only) {
      margin-left: 25px;
      width: 182px;
      font-size: 16px;
      line-height: 140%;
    }
  }

  @include close-open;

  @media (max-width: $mobile-width-only) {
    h4::after {
      top: 30px;
      right: 20px;
    }
  }

  p {
    margin-left: 50px;
    margin-top: 20px;
    max-width: 527px;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;

    @media (max-width: $tablet-width-only) {
      margin-top: 35px;
    }

    @media (max-width: $mobile-width-only) {
      margin-left: 25px;
      width: 204px;
      font-size: 14px;
      line-height: 140%;
    }
  }

  @media (max-width: $mobile-width-only) {
    padding-top: 22px;
    padding-bottom: 25px;
  }
}
