.login {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  margin: auto;
  display: none;
  background-color: $main-ground;
  font-family: "Poppins", "Arial", sans-serif;

  h1 {
    margin-top: 60px;
    margin-bottom: 45px;
    font-family: "Cormorant Garamond", "Times New Roman", serif;
    font-weight: 700;
    font-size: 34px;
    line-height: 130%;
    text-align: center;
    color: $letter;
  }

  p {
    margin-top: 0;
    margin-bottom: 50px;
    text-align: right;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $grey-letter;
  }
}

.login__logo {
  display: block;
  margin: 0 auto;
}

.login--show {
  display: block;
  width: 430px;
  height: 605px;
  box-shadow: 0 4px 30px $black;
}

.login--no-js {
  height: 616px;
}

.login__form {
  margin-left: auto;
  margin-right: auto;
  max-width: 340px;

  input {
    margin-bottom: 10px;
    padding-bottom: 9px;
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid $letter;
    background-color: $main-ground;
    font-size: 14px;
    line-height: 21px;

    &:focus,
    &:hover {
      opacity: 0.7;
    }
  }
}

.login__submit {
  position: relative;
  display: block;
  margin-bottom: 20px;
  width: 100%;
  height: 68px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: $white;
  background-color: $under-line;
  border: none;
}

.login__close {
  @include close-button;
}

.login__no-account {
  p {
    text-align: center;
    font-size: 14px;
    line-height: 21px;
  }

  span {
    color: $letter;
  }
}
